import React from "react";

export const wrapper = (Child, cutonFallBack) => {
     // jsx
  if (Child.type && !Child._init && !Child._payload) {
    return Child
  } else {
    // clasfunction
    if (typeof Child === 'function') {
      return <Child></Child>
    } else {
      // lazy
      return (
        <React.Suspense fallback={cutonFallBack || <>...</>}>
          {<Child></Child>}
        </React.Suspense>
      )
    }
  }
}